<div class="main-grid-body"><div class="main-grid-row main-grid-row-body" v-for="task in tasksList"><div class="main-grid-cell" v-for="taskItem in task" :class="taskItem.name"><div class="main-grid-cell-inner"><span class="main-grid-cell-content"><a class="main-grid-cell-task" v-if="taskItem.name!='title'" :class="{'ui-label ui-label-default ui-label-fill ui-label-link':taskItem.name==='deadline'}">{{ taskItem.value.name?taskItem.value.name:taskItem.value}}</a><router-link class="main-grid-cell-task" :to="'/item/'+taskItem.value.id" v-if="taskItem.name=='title'" :class="{'ui-label ui-label-default ui-label-fill ui-label-link':taskItem.name==='deadline'}">{{ taskItem.value.title}}</router-link></span></div></div></div><div class="main-grid-loader" v-if="loader"><img class="main-grid-loader-gif" alt="loader.gif" src="@/assets/img/elems/loader.gif"></div></div>